import React, { Component } from 'react';
import InputBox from '../../InputBox/InputBox.component';
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import CTA from '../../CTA/CTA.component';
import './QuestionnaireCardName.styles.scss';

class QuestionnaireCardName extends Component {

    constructor(props) {
        super(props);
        this.firstNameInput = React.createRef();
        this.lastNameInput = React.createRef();
    }

    render() {
        const {nameAction} = this.props;
        return (
            <section className='QuestionnaireCardName' >
                <QuestionnaireTitle >How do you want the information sent?</QuestionnaireTitle>
                <InputBox ref={this.firstNameInput} placeholder='First Name' name='firstName' />
                <InputBox ref={this.lastNameInput} placeholder='Last Name' name='lastName' />
                <CTA onClick={() => nameAction([this.firstNameInput.current.value, this.lastNameInput.current.value])} >Next</CTA>
            </section>
        );
    }
}

export default QuestionnaireCardName;