import quoteFormTypes from './quoteForm.types';
const { ZIPCODE, EBILL, CONTACT, NAME, NO, YES } = quoteFormTypes

export const zipCode = (payload) => ({
    type: ZIPCODE,
    payload: payload
});

export const eBill = (payload) => ({
    type: EBILL,
    payload: payload
});

export const contact = (payload) => ({
    type: CONTACT,
    payload: payload
});

export const name = (payload) => ({
    type: NAME,
    payload: payload
});

export const no = () => ({
    type: NO
})

export const yes = () => ({
    type: YES
})