import quoteFormTypes from './quoteForm.types';

const { ZIPCODE, EBILL, CONTACT, NAME, NO, YES } = quoteFormTypes;

const INITIAL_STATE = {
    ownsHome: undefined,
    zipCode: '',
    eBill: '',
    contact: [],
    name: []
}

const quoteFormReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ZIPCODE:
            return {
                ...state,
                zipCode: action.payload
            }
        
        case EBILL:
            return {
                ...state,
                eBill: action.payload
            }
        
        case CONTACT:
            return {
                ...state,
                contact: action.payload
            }
        
        case NAME:
            return {
                ...state,
                name: action.payload
            }
        case NO:
            return {
                ...state,
                ownsHome: false
            }
        case YES:
            return {
                ...state,
                ownsHome: true
            }
        default:
            return state;
    }
}

export default quoteFormReducer;