import CardTypes from './card.types';

const INITIAL_STATE = {
    card: ''
}

const cardReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case CardTypes.ACTIVATE_CARD:
            return {
                ...state,
                card: action.payload
            }
        default:
            return state;
    }
}

export default cardReducer;