import React, { Component } from 'react';
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import './QuestionnaireCardBill.styles.scss';
import CTA from '../../CTA/CTA.component';

class QuestionnaireCardBill extends Component {
    constructor(props) {
        super(props);
        this.eBillInput = React.createRef();
    }

    render() {

        const {eBillAction} = this.props

        return (
            <section className='QuestionnaireCardBill' >
                <section className='QuestionnaireCardBill__Blur' />
                <section className='QuestionnaireCardBill__Container' >
                    <section className='QuestionnaireCardBill__Container__Top' >
                        <QuestionnaireTitle >What is your average Electrical Bill?</QuestionnaireTitle>
                    </section>
                    <section className='QuestionnaireCardBill__Container__Bottom' >
                        <input ref={this.eBillInput} id='eBillRange' className='QuestionnaireCardBill__Container__Bottom__Input' type='range' min='0' max='800' />
                        <CTA onClick={() => eBillAction(this.eBillInput.current.value)} >Next</CTA>
                    </section>
                </section>
            </section>
        );
    }
};

export default QuestionnaireCardBill;