import ModalActionTypes from './modal.types';

const INITIAL_STATE = {
    modalCardsIsOpen: false,
    modalQuestionnaireIsOpen: false
}

const modalReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ModalActionTypes.TOGGLE_MODAL_CARDS:
            return {
                ...state,
                modalCardsIsOpen: !state.modalCardsIsOpen
            }
        case ModalActionTypes.TOGGLE_MODAL_QUESTIONNAIRE:
            return {
                ...state,
                modalQuestionnaireIsOpen: !state.modalQuestionnaireIsOpen
            }
        default:
            return state;
    }
}

export default modalReducer;