import React, {Component} from 'react';
import './QuestionnaireCardHome.styles.scss'
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import QuestionnaireSubTitle from '../../QuestionnaireSubTitle/QuestionnaireSubTitle.component';
import InputBox from '../../InputBox/InputBox.component'

class QuestionnaireCardHome extends Component { 
    constructor(props) {
        super(props);
        this.zipCodeInput = React.createRef();
    }

    render() {
        const {zipCodeAction, noAction, yesAction} = this.props;
        const handler = () => {
            zipCodeAction(this.zipCodeInput.current.value);
            return yesAction();
        }
        return (
            <section className='QuestionnaireCardHome' >
                <section className='QuestionnaireCardHome__Blur' />
                <section className='QuestionnaireCardHome__Container'>
                    <section className='QuestionnaireCardHome__Container__Top' >
                        <QuestionnaireTitle >Do you own your home?</QuestionnaireTitle>
                    </section>
                    <section className='QuestionnaireCardHome__Container__Bottom' >
                    <QuestionnaireSubTitle ><InputBox name='zipCode' placeholder='Zip Code' ref={this.zipCodeInput} ></InputBox> <span className='QuestionnaireCardHome__Container__Bottom__Span' onClick={handler} >Yes</span> or <span className='QuestionnaireCardHome__Container__Bottom__Span' onClick={noAction} >No</span></QuestionnaireSubTitle>
                    </section>
                </section>
                
            </section>
    );
    }
};

export default QuestionnaireCardHome;