import React, { Component } from 'react';
import { connect } from 'react-redux'
import './QuestionnaireViewer.styles.scss'
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import InputBox from '../../InputBox/InputBox.component'

import { zipCode, eBill, contact, name, no, yes } from '../../../redux/quoteForm/quoteForm.actions';
import QuestionnaireSubTitle from '../../QuestionnaireSubTitle/QuestionnaireSubTitle.component';
import QuestionnaireCardHome from '../../QuestionnaireCards/QuestionnaireCardHome/QuestionnaireCardHome.component';
import QuestionnaireCardBill from '../../QuestionnaireCards/QuestionnaireCardBill/QuestionnaireCardBill.component';
import QuestionnaireCardContact from '../../QuestionnaireCards/QuestionnaireCardContact/QuestionnaireCardContact.component';
import QuestionnaireCardName from '../../QuestionnaireCards/QuestionnaireCardName/QuestionnaireCardName.component';

class QuestionnaireViewer extends Component {

    constructor(props) {
        super(props)
    }
    
    render() {

        const { onClick, ownsHome, zipCode, eBill, contact, name, zipCodeAction, yesAction, noAction, eBillAction, contactAction, nameAction } = this.props

        return (
            <section className='QuestionnaireViewer' onClick={onClick} >
                <section className='QuestionnaireViewer__Container' onClick={e => e.stopPropagation()} >
                    {
                        ownsHome === undefined ? <QuestionnaireCardHome noAction={noAction} yesAction={ yesAction} zipCodeAction={zipCodeAction} /> :
                        ownsHome === false ? <QuestionnaireTitle >Sorry, Solar solar only works if you are a Homeowner.</QuestionnaireTitle> :
                        eBill === '' ? <QuestionnaireCardBill eBillAction={eBillAction} /> : 
                        contact.length < 1 ? <QuestionnaireCardContact contactAction={contactAction} /> :
                        name.length < 1 ? <QuestionnaireCardName nameAction={nameAction} /> : <QuestionnaireTitle >Thank you, we will contact you shortly!</QuestionnaireTitle>

                    }
                    <p className='QuestionnaireViewer__Container__X' onClick={onClick} >X</p>
                </section>
            </section>
        );
    }
};

const mapStateToProps = ({ quoteForm: { ownsHome, zipCode, eBill, contact, name } }) => ({
    ownsHome,
    zipCode,
    eBill,
    contact,
    name
})

const mapDispathToProps = dispatch => ({
    zipCodeAction: (zipNumber) => dispatch(zipCode(zipNumber)),
    eBillAction: (billAmount) => dispatch(eBill(billAmount)),
    contactAction: (contactOptions) => dispatch(contact(contactOptions)),
    nameAction: (names) => dispatch(name(names)),
    noAction: () => dispatch(no()),
    yesAction: () => dispatch(yes())
})

export default connect(mapStateToProps, mapDispathToProps)(QuestionnaireViewer);