import React, {Component} from 'react';
import './QuestionnaireCardContact.styles.scss';
import InputBox from '../../InputBox/InputBox.component';
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import CTA from '../../CTA/CTA.component';

class QuestionnaireCardContact extends Component {

    constructor(props) {
        super(props);
        this.nameInput = React.createRef();
        this.phoneInput = React.createRef();
    }

    render() {
        let contactList = [];
        return (
            <section className='QuestionnaireCardContact' >
                <QuestionnaireTitle >How do you want the information sent?</QuestionnaireTitle>
                <InputBox ref={this.phoneInput} placeholder='Phone #' name='phoneNumber' />
                <InputBox ref={this.nameInput} placeholder='Email @' name='email' />
                <CTA onClick={() => this.props.contactAction([this.nameInput.current.value, this.phoneInput.current.value])} >Next</CTA>
            </section>
        );
    }
};

export default QuestionnaireCardContact;